import 'owl.carousel';
import header from './includes/header';
function useBannerCarousel() {
    if (window.innerWidth < 740)
        return;
    $('.banner-slider').owlCarousel({
        items: 1,
        dots: false,
        nav: true,
        navClass: ['owl-prev banner-slider__nav', 'owl-next banner-slider__nav'],
        navText: [
            '<img src="/assets/images/banner-carousel-btn-prev.svg" alt="banner-carousel-btn-prev" />',
            '<img src="/assets/images/banner-carousel-btn-next.svg" alt="banner-carousel-btn-next" />',
        ],
        margin: 30
    });
}
function main() {
    header();
    useBannerCarousel();
}
main();
